<script>
import Layout from '@layouts/main'
import PageHeader from '@components/page-header'
import axios from 'axios';
import Utils from '../../../../../utils/Util'

/**
 * Invoice component
 */
export default {
	page: {
		title: 'Sertifikat',
	},
	components: { Layout, PageHeader },
	data() {
		return {
			title: 'Sertifikat'+ ' ' +this.$route.params.action,
			api: Utils.Host(),
			message: "",
			data:[],
			options: "",
			aksiEdit: false,
      		aksiSave: false,
			datastatus: [ { "f_kode": "LULUS", "f_nama": "LULUS" }, { "f_kode": "TIDAK LULUS", "f_nama": "TIDAK LULUS"} ], 
			modelstatus:"",
			datajenissertifikat: [],
			f_k_f_sertifikat_id:"",
			alert:false,
		}
	},
	mounted() {
		console.log("alamat", Utils.Host())
		this.Getdata()
		this.Getfilter()
		if (this.$route.params.action === 'tambah') {
		this.aksiEdit = true
		this.aksiSave = true
		}
		if (this.$route.params.action === 'edit') {
		this.aksiEdit = true
		this.aksiSave = true
		}
		if (this.$route.params.action === 'detail') {
		this.aksiEdit = false
		this.aksiSave = false
    }
  },
//   computed: {
//   },
  methods: {
    async Getdata() {
      try {
        const action = this.$route.params.action
        if (action === 'detail' || action === 'edit') {
          let response = await axios.get(this.api+"/sertifikat/"+this.$route.params.id, Utils.authHeader());
          this.data = response.data.Data[0];
        }
      } catch (err) {
          console.error(err);
      }
    },
	async Getfilter() {
        try {
			let response = await axios.get(this.api+"/sertifikatref", Utils.authHeader());
			this.datajenissertifikat = response.data.Data;
			for (const tampil of response.data.Data) {
				tampil.f_kode = tampil.f_sertifikat_id;
				tampil.f_nama = tampil.f_tahun;
			}
        } catch (err) {
            console.error(err);
        }
    },
    async openEdit(id) {
        document.location = '/sertifikataction/edit/' + id
        // location.reload();
    },
    validation() {
      if (!this.data.f_nama) {
          return { code: 500, message: 'Nama Data ini harus diisi' }
      } else if (!this.data.f_pencarian) {
          return { code: 500, message: 'Pencarian Data ini harus diisi' }
      } else {
          return { code: 200, message: '' }
      }
    },
    async onSave() {
        const validation = this.validation()
        if (validation.code === 200) {
        var res = null
        const action = this.$route.params.action;
		const target = { f_nama: this.data.f_nama, f_tambahan_1_value: this.data.f_tambahan_1_value, f_tambahan_2_value: this.data.f_tambahan_2_value, f_tambahan_3_value: this.data.f_tambahan_3_value, f_tahun: this.data.f_tahun, f_nomor: this.data.f_nomor, f_tambahan_1_label: this.data.f_tambahan_1_label, f_tambahan_2_label: this.data.f_tambahan_2_label, f_tambahan_3_label: this.data.f_tambahan_3_label, f_pencarian: this.data.f_pencarian, f_k_f_sertifikat_id: this.data.f_k_f_sertifikat_id, f_status: this.data.modelstatus.f_kode };
        if (action === 'tambah') {
            res = await axios.post(this.api+"/sertifikat", target, Utils.authHeader());
        } else {
            res = await axios.put(this.api+"/sertifikat/"+ this.$route.params.id, target, Utils.authHeader());
        }
        if (res.data.Error_code === 0) {
			this.alert = true
			this.message = res.data.Error_desc
            document.location = '/sertifikataction/detail/' + res.data.Data[0].f_sertifikat_id;
            this.Getdata();
            this.aksiEdit = !this.aksiEdit
            this.aksiSave = false
        }  else {
			this.alert = true
			this.message = res.data.Error_desc
        }
        } else {
			this.alert = true
			this.message = validation.message
        }
    },
  },
}
</script>

<template>
	<Layout>
		<PageHeader :title="title" :items="items" class="d-print-none" />
		<div class="row">
			<div class="col-12">
				<div class="card">
					<div class="card-body">
						<!-- Logo & title -->
						
						<div class="row">
							<div class="col-12">
								<div class="table-responsive">
									
									<b-alert
											v-model="alert"
											variant="success"
											dismissible
											>{{ message }}</b-alert
										>

									<br>&nbsp;&nbsp;
									<br>
									<div class="table-responsive">

									<b-form-group
										label-cols-sm="10"
										label-cols-lg="2"
										label="Tahun"
										label-for="input-horizontal"
									>
									<b-form-input
										id="input-1"
										v-model="data.f_tahun"
										type="text"
										required
										placeholder="Tahun"
									></b-form-input>
									</b-form-group>
									
									<b-form-group
										label-cols-sm="10"
										label-cols-lg="2"
										label="Nama"
										label-for="input-horizontal"
									>
									<b-form-input
										id="input-1"
										v-model="data.f_nama"
										type="text"
										required
										placeholder="Nama"
									></b-form-input>
									</b-form-group>

									<b-form-group
										label-cols-sm="10"
										label-cols-lg="2"
										label="Label 1"
										label-for="input-horizontal"
									>
									<b-form-input
										id="input-1"
										v-model="data.f_tambahan_1_label"
										type="text"
										required
										placeholder="Label 1"
									></b-form-input>
									</b-form-group>

									<b-form-group
										label-cols-sm="10"
										label-cols-lg="2"
										label="Tambahan 1"
										label-for="input-horizontal"
									>
									<b-form-input
										id="input-1"
										v-model="data.f_tambahan_1_value"
										type="text"
										required
										placeholder="Tambahan 1"
									></b-form-input>
									</b-form-group>

									<b-form-group
										label-cols-sm="10"
										label-cols-lg="2"
										label="Label 2"
										label-for="input-horizontal"
									>
									<b-form-input
										id="input-1"
										v-model="data.f_tambahan_2_label"
										type="text"
										required
										placeholder="Label 2"
									></b-form-input>
									</b-form-group>

									<b-form-group
										label-cols-sm="10"
										label-cols-lg="2"
										label="Tambahan 2"
										label-for="input-horizontal"
									>
									<b-form-input
										id="input-1"
										v-model="data.f_tambahan_2_value"
										type="text"
										required
										placeholder="Tambahan 2"
									></b-form-input>
									</b-form-group>

									<b-form-group
										label-cols-sm="10"
										label-cols-lg="2"
										label="Label 3"
										label-for="input-horizontal"
									>
									<b-form-input
										id="input-1"
										v-model="data.f_tambahan_3_label"
										type="text"
										required
										placeholder="Label 3"
									></b-form-input>
									</b-form-group>

									<b-form-group
										label-cols-sm="10"
										label-cols-lg="2"
										label="Tambahan 3"
										label-for="input-horizontal"
									>
									<b-form-input
										id="input-1"
										v-model="data.f_tambahan_3_value"
										type="text"
										required
										placeholder="Tambahan 3"
									></b-form-input>
									</b-form-group>

									<b-form-group
										label-cols-sm="10"
										label-cols-lg="2"
										label="Nomor"
										label-for="input-horizontal"
									>
									<b-form-input
										id="input-1"
										v-model="data.f_nomor"
										type="text"
										required
										placeholder="Nomor"
									></b-form-input>
									</b-form-group>

									<b-form-group
										label-cols-sm="10"
										label-cols-lg="2"
										label="Pencarian"
										label-for="input-horizontal"
									>
									<b-form-input
										id="input-1"
										v-model="data.f_pencarian"
										type="text"
										required
										placeholder="Pencarian"
									></b-form-input>
									</b-form-group>

									<b-form-group
										label-cols-sm="10"
										label-cols-lg="2"
										label="Status"
										label-for="input-horizontal"
									>
									<b-form-select v-model="data.modelstatus">
										<option v-for='(value, key) in datastatus' :value='value'>{{value.f_nama}}</option>
									</b-form-select>
									</b-form-group>	

									<b-form-group
										label-cols-sm="10"
										label-cols-lg="2"
										label="Jenis Sertifikat"
										label-for="input-horizontal"
									>
									<b-form-select v-model="data.f_k_f_sertifikat_id">
										<option v-for='(value, key) in datajenissertifikat' :value='value.f_kode'>{{value.f_nama}} - {{value.f_jenis}}</option>
									</b-form-select>
									</b-form-group>	

									<b-form-group
										label-cols-sm="10"
										label-cols-lg="2"
										label-for="input-horizontal"
									>
									<b-button variant="success" @click="onSave(data.f_sertifikat_id)" v-if="aksiEdit">Simpan Data</b-button>
									&nbsp;
									<b-button variant="danger" @click="openEdit(data.f_sertifikat_id)" v-if="!aksiEdit">Edit Data</b-button>
									</b-form-group>							

									</div>

								</div>
								<!-- end table-responsive -->
							</div>
							<!-- end col -->
						</div>
						<!-- end row -->

	
					</div>
				</div>
			</div>
			<!-- end col -->
		</div>
		<!-- end row -->
		<!-- end row -->
	</Layout>
</template>
